.homepage-container {
  display: flex;
  flex-direction: column;
  padding: 5% 15%;
  justify-content: center;
}
.homepage-container h2 {
  color: rgba(71, 150, 168, 1);
  margin-bottom: 3rem;
}
.form_container {
  display: grid;
  width: 100%;
  grid-template-columns: 45% 45%;
  justify-content: space-between;
}
.form_container label {
  display: flex;
  font-size: 0.8rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: rgba(22, 62, 70, 1);
}
.formInput {
  display: grid;
  height: 3.2rem !important;
  width: 90%;
  margin-bottom: 2rem;
  border-radius: 8px;
  border: 1px solid rgba(27, 80, 91, 0.2);
  outline: gray;
  padding: 0.2rem 5%;
}
.formDate {
  display: flex;
  height: 3.2rem !important;
  width: 90%;
  margin-bottom: 3rem;
  border-radius: 8px;
  border: 1px solid rgba(27, 80, 91, 0.2);
  outline: gray;
  padding: 0.2rem 5%;
}

.formButton {
  margin-top: 20px;
  background: rgba(73, 195, 222, 1);
  color: white;
  cursor: pointer;
  width: 100%;
  height: 3rem;
  outline: none;
  padding: 1rem 2rem;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  text-align: center;
  text-decoration: none;
  border: none;
  align-items: center;
}
.thankYou {
  height: 100vh;
  width: 100%;
  background: #d3d2d2;
}
.thankYou .thankYouHeader {
  height: 40%;
  padding: 2%;
  background-image: linear-gradient(
      90.03deg,
      #11172c 36.91%,
      rgba(17, 23, 44, 0.1)
    ),
    url("../../assets/aws-bg.jpg");
  background-position: right center;
  color: rgba(73, 195, 222, 1);
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.thankYou .picContainer {
  display: flex;
}
.thankYou .picContainer img {
  height: 3rem;
  object-fit: contain;
  margin-right: 15px;
  margin-left: 15px;
}
.thankYou .picContainer hr {
  height: 3rem;
}
.thankYou .thankYou-wrapper {
  position: relative;
  padding: 4rem;
  background: white;
  border-top: 1rem solid #11172c98;
  width: 80%;
  left: 0;
  right: 0;
  margin: auto;
  top: -4rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.thankYou-wrapper > p {
  display: flex;
  gap: 1rem;
  align-items: center;
}
.thankYou-wrapper > p > svg {
  color: green;
  font-size: 36px;
}
.thankYou-wrapper h1 {
  font-size: 24px;
  font-weight: 700;
  margin: 0;
}
.thankYou-wrapper h2 {
  font-size: 16px;
}
.thankYou-wrapper .back {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.thankYou-wrapper .back svg {
  font-size: 24px;
  color: #11172c;
}
.thankYou-wrapper p {
  font-size: 16px;
  font-weight: 500;
  margin: 0;
}

@media screen and (max-width: 600px) {
  .homepage-container {
    padding: 5% 10%;
  }
  .form_container {
    display: grid;
    width: 100%;
    grid-template-columns: 100%;
    justify-content: space-between;
  }
  .formInput {
    width: 100%;
    margin-bottom: 1.8rem;
  }

  .formDate {
    width: 100%;
  }
}
